.accordion__item {
  margin-top: -4px;

  @include vp-767 {
    margin-top: 0;
    padding: 19px 25px 16px 25px;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.accordion__item ul {
  @include vp-767 {
    padding-top: 13px;
  }
}

.accordion__item > ul {
  @include vp-767 {
    display: none;
  }
}

.accordion button {
  padding: 0;

  @include vp-767 {
    position: relative;
    user-select: none;

    &::before,
    &::after {
      position: absolute;
      content: "";
      width: 15px;
      height: 1px;
      display: block;
      background-color: #c4c4c4;
      top: 5px;
      right: 2px;
    }

    &::after {
      transform: rotate(90deg);
    }
  }
}

.accordion__item.is-open button {
  @include vp-767 {
    cursor: pointer;

    &::after {
      display: none;
    }
  }
}

.accordion__chapter {
  position: relative;
}

.accordion__chapter-button {
  display: none;

  @include vp-767 {
    display: block;
    position: absolute;
    background-color: transparent;
    padding: 0;
    border: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.accordion__chapter h2 {
  color: #ffffff;
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-white;
  background-color: transparent;
}
